<template>
  <img :src="imageUrl" :style="style" />
</template>
<script>
import ScrollOut from "../plugins/scroll-out";

export default {
  name: "ImageLoader",
  props: {
    image: String
  },
  data: function() {
    return {
      imageUrl: this.isMobile()
        ? this.postfix(this.image, "_mobile_preview")
        : this.postfix(this.image, "_preview"),
      style: {
        filter: "blur(50px)"
      }
    };
  },
  mounted() {
    this.so = ScrollOut({
      targets: this.$el,
      onChange: this.load
    });
  },
  methods: {
    load: function(element, ctx) {
      if (ctx.visible !== 1) {
        return;
      }
      // Job of ScrollOut is done
      this.so.teardown();

      let img = new Image();
      let start = Date.now();

      img.onload = () =>
        setTimeout(() => {
          this.setImg(img.src);
          this.style = {
            filter: blur(0),
            transition: this.isMobile()
              ? "filter 0.5s ease-in "
              : "transform 0.1s linear, filter 0.5s ease-in"
          };
        }, Math.max(400 - (Date.now() - start), 0));

      // if connections is broken try again
      img.onerror = () =>
        setTimeout(() => {
          this.mounted();
        }, 500);

      img.src = this.isMobile()
        ? this.postfix(this.image, "_mobile")
        : this.postfix(this.image, "");
    },
    setImg: function(url) {
      this.imageUrl = url;
    },
    isMobile: function() {
      return window.matchMedia("(max-width: 767px)").matches;
    },
    postfix: function(file, postfix) {
      return (
        file.substring(0, file.lastIndexOf("/")) +
        "/target" +
        file.substring(file.lastIndexOf("/"), file.lastIndexOf(".")) +
        postfix +
        file.substring(file.lastIndexOf("."))
      );
    }
  }
};
</script>


