<template>
  <div class="apple-watch-section">
    <div
      class="apple-watch-figure"
      :style="{'--scale':scale , transition : transition, '--brightness':brightness}"
    >
      <slot></slot>
    </div>
    <!-- <div class="top-space" />-->

    <div class="space">
      <div class="top-space" />
      <apple-watch-content
        :opac="opacity"
        :photocredit="this.photocredit"
        :licenseName="this.licenseName"
        :licenseUrl="this.licenseUrl"
        :title="this.title"
        :time="this.time"
      />
    </div>
    <!-- <div class="bottom-space">

    </div>-->
  </div>
</template>
<script>
import ScrollOut from "../plugins/scroll-out";
import AppleWatchContent from "./AppleWatchContent";

export default {
  name: "AppleWatch",
  props: {
    photocredit: String,
    licenseName: String,
    licenseUrl: String,
    title: String,
    time: String
  },
  components: {
    AppleWatchContent
  },
  mounted() {
    this.so = ScrollOut({
      targets: this.$el,
      onChange: this.function,
      onShown: this.isVisible
    });
    this.transition = this.isMobile() ? "" : "transform 0.1s linear";
  },
  destroyed() {
    this.so.teardown();
  },
  data() {
    return {
      scrollY: 0,
      scaleLimit: 0.9,
      transition: ""
    };
  },
  computed: {
    scale: function() {
      return Math.min(this.scaleLimit + this.scrollY / 10, 1);
    },
    opacity: function() {
      return Math.pow((this.scale - this.scaleLimit) * 10, 2);
    },
    brightness: function() {
      return 1 - Math.pow((this.scale - this.scaleLimit) * 10, 2) / 2;
    }
  },
  methods: {
    function(element, ctx) {
      this.scrollY = ctx.viewportY + 1;
    },
    isMobile: function() {
      return window.matchMedia("(max-width: 767px)").matches;
    },
    isVisible: function() {
      this.$emit("visible");
    }
  }
};
</script>
