  position: flex;
  min-height: 200vh;
  width: 100vw;
  background: white;
  display: block;
  flex-direction: column;
}

.apple-watch-figure {
  position: sticky;
  top: 0;
  margin: 0;
  display: block;
  overflow: hidden;
  flex: none;
}

.apple-watch-figure {
  width: 100vw;
  height: 100vh;
  margin: 0;
  position: top;
  top: 0;
  left: 0;
  overflow: hidden;
  > img {
    // 🥕
    overflow: hidden;
    display: block;
    width: 100%;
    height: 100%;
    max-width: 100%;
    object-fit: cover;
    object-position: top top;
  }
}

.apple-watch-figure {
  filter: brightness(var(--brightness));
  transform-origin: center;
  position: sticky;
  position: -webkit-sticky;
  margin-top: 0;
  display: block;
  overflow: hidden;
  will-change: transform, filter;
  transform: scale(var(--scale));
  // transition: transform 0.1s linear;

  img {
    display: block;
    overflow: hidden;
    transform-origin: center;
    will-change: transform;
    transform: scale(calc(1 / var(--scale)));
    // transition: transform 0.1s linear;
  }
}

.space {
  top: 0;
  position: absolute;
  height: 200vh;
}

.top-space {
  position: relative;
  height: 50vh;
}

.bottom-space {
  position: relative;
  height: 50vh;
}

*,
*:before,
*:after {
  position: relative;
  box-sizing: border-box;
}
</style>